
.card {
  width: 90%;
  height: 200px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  overflow: hidden;
  margin: 2rem;
}
.card img {
    -ms-flex-negative: 0;
        flex-shrink: 0;
    max-width: 100%;
    max-height: 100%;
}
@media only screen and (min-width: 670px) {
.card {
      width: 40%;
}
.card img {
        margin-bottom: 3rem;
}
}



